import React from 'react';

function IndicatorTabs({currentIndicator, handleIndicatorChange}) {

    return (
      <>
        <button
          onClick={() => handleIndicatorChange('VWAPRSI')}
          className={currentIndicator === 'VWAPRSI' ? 'script-tab-active' : 'script-tab-inactive'}
        >
          VWAP-RSI
        </button>
        <button
          onClick={() => handleIndicatorChange('StochRSI')}
          className={currentIndicator === 'StochRSI' ? 'script-tab-active' : 'script-tab-inactive'}
        >
          Stoch-RSI
        </button>
      </>
    );
}

export default IndicatorTabs;