import React, { useState } from 'react';
import './styles/toolbar.css';
import './styles/toolbarMediaQueries.css';
import Algorithms from './AlgorithmTabs.js';
import Indicators from './IndicatorTabs.js';
import BTCBot from './algorithms/BTCBot.js';
import ETHBot from './algorithms/ETHBot.js';
import VWAPRSI from './indicators/VWAPRSI.js';
import StochRSI from './indicators/StochRSI.js';

function Toolbar() {

  const [currentTool, setCurrentTool] = useState('Indicators')

  const handleToolChange = (tool) => setCurrentTool(tool)

  const [currentAlgorithm, setCurrentAlgorithm] = useState('BTCBot');
  const [currentIndicator, setCurrentIndicator] = useState('VWAPRSI');

  const renderAlgorithm = () => {
    if (currentAlgorithm === 'BTCBot' && currentTool === "Algorithms") {
      return <BTCBot />;
    }
    if (currentAlgorithm === 'ETHBot' && currentTool === "Algorithms") {
      return <ETHBot />;
    }
    return
  };
  const renderIndicator = () => {
    if (currentIndicator === 'VWAPRSI' && currentTool === "Indicators") {
      return <VWAPRSI />;
    }
    if (currentIndicator === 'StochRSI' && currentTool === "Indicators") {
      return <StochRSI />;
    }
    return
  };

  const handleAlgorithmChange = (algorithm) => setCurrentAlgorithm(algorithm);
  const handleIndicatorChange = (indicator) => setCurrentIndicator(indicator);

  return (
    // Contains all elements, split into two divisions that are in flex row
    <div className="toolbar-container">

      {/* First flex column, contains the tabs for selecting script type as well as specific script demo */}
      <div className="tabs-container">

        {/* Contains tabular script selection */}
        <div className="script-toggle-container">
          <label className="script-toggle-label">
            Select the type of script to demo
          </label>
          <div className="script-toggle-wrap">
            <button
              className={currentTool === 'Indicators' ? 'tooltab tooltab-active' : 'tooltab tooltab-inactive'}
              onClick={() => handleToolChange('Indicators')} 
            >
              Indicators
            </button>
            <button 
              className={currentTool === 'Algorithms' ? 'tooltab tooltab-active' : 'tooltab tooltab-inactive'}
              onClick={() => handleToolChange('Algorithms')} 
            >
              Algoirthms
            </button>
          </div>
        </div>

        {/* Contains the tabs to select the script demo to display */}
        <div className="demo-toggle-container">
          <label className="demo-toggle-label">
            Select the {
              currentTool === "Algorithms" ? "algorithm" : "indicator"
            } to demo
          </label>
          {/* Based on state set by user selected tabs (following container), this renders the appropriate list of to demo the scripts (Indicator or Algorithms) */}
          { 
            currentTool === "Algorithms" ? <Algorithms currentAlgorithm={currentAlgorithm} handleAlgorithmChange={handleAlgorithmChange}/> : <Indicators currentIndicator={currentIndicator} handleIndicatorChange={handleIndicatorChange}/>
          }
        </div>
      </div>

      {/* Second flex column, contains "Trading Tools" title and the window where the demos are rendered */}
      <div className="demo-container">

        {/* Contains component title */}
        <div className='toolbar-title-container'>
          <div className='toolbar-title-wrap'>
            <h2 className="toolbar-title">
              Trading Tools
            </h2>
            <p className='toolbar-subtitle'>
              A quick snapshot
            </p>
          </div>
        </div>

        {/* Contains demo window */}
        <div className="script-demo-container">
          {renderIndicator()}
          {renderAlgorithm()}
        </div>

      </div>

    </div>
  );
};

export default Toolbar;
