import React from 'react';

function AlgorithmTabs({currentAlgorithm, handleAlgorithmChange}) {

  return (
    <>
      <button
        onClick={() => handleAlgorithmChange('BTCBot')}
        className={currentAlgorithm === 'BTCBot' ? 'script-tab-active' : 'script-tab-inactive'}
      >
        BTC Bot
      </button>
      <button
        onClick={() => handleAlgorithmChange('ETHBot')}
        className={currentAlgorithm === 'ETHBot' ? 'script-tab-active' : 'script-tab-inactive'}
      >
        ETH bot
      </button>
    </>
  );
}

export default AlgorithmTabs;